import { NextFunction, Response } from "express";
import { verify } from "jsonwebtoken";
import { SECRET_KEY } from "@/config/env";
import { HttpException } from "@exceptions/httpException";
import { DataStoredInToken, RequestWithUser } from "@interfaces/authInterface";
import userModel from "@/models/User";

const authMiddleware = async (req: RequestWithUser, res: Response, next: NextFunction) => {
  try {
    const Authorization =
      req.cookies["Authorization"] ||
      (req.header("Authorization") ? req.header("Authorization").split("Bearer ")[1] : null);

    if (Authorization) {
      const secretKey: string = SECRET_KEY;
      const verificationResponse = verify(Authorization, secretKey) as DataStoredInToken;
      const adminId = verificationResponse._id;
      const findUser = await userModel.findById(adminId);

      if (findUser) {
        req.user = findUser;
        next();
      } else {
        next(new HttpException(401, "Wrong authentication token"));
      }
    } else {
      next(new HttpException(404, "Authentication token missing"));
    }
  } catch (error) {
    next(new HttpException(401, "Wrong authentication token"));
  }
};

export default authMiddleware;